import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IFondo } from '../models/fondo';

@Injectable()
export class FondosService {
    constructor(private http: HttpClient) { }
/**
 * -Funcion en el servicio- 
 * - Esta funcion retorna los fondos.
 * @returns fondos
 */
getFondos(sort?:any): Observable<IFondo[]> {
    let f = '?';
    f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';

    var subject = new Subject<any>();

    this.http.get<IFondo[]>(`${environment.fondos.view_fondos}${f}`, { observe: 'response' })
        .subscribe((resp: any) => {
            subject.next({
                'list': resp.body,
            })
        });
    return subject.asObservable();
}
    /**
     * Funcion en el Servicio 
     * @param fondo 
     * @returns Un fondo nuevo
     */
    saveFondo(fondo: IFondo): Observable<IFondo> {
        return this.http.post<IFondo>(`${environment.fondos.fondos_token}`, fondo);
    }
    /**
     * Funcion en el servicio
     * @param fondo 
     * @returns fondo actualizado
     */
    updateFondo(fondo: IFondo): Observable<IFondo> {
        return this.http.put<IFondo>(`${environment.fondos.fondos_token}` + '/' + fondo.idfondo, fondo);
    }
    /**
     * Funcion en el servicio
     * @param idfondo 
     * @returns Elimina el fondo
     */
    deleteFondo(idfondo: number): Observable<IFondo> {
        return this.http.delete<IFondo>(environment.fondos.fondos_token + '/' + idfondo);
    }

}