import { AfterViewInit, Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { IFondo } from '../../models/fondo';
import { FondosService } from '../../service/fondos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IAlertConfig } from '@shared/models/alert';
import { finalize, Subject, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { AppAlertService } from '@shared/service/app/alert.service';
import { EmpresaService } from '@shared/service/app/empresa.service';
@Component({
    selector: 'app-save-fondo',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['descripcion', 'activo', 'acciones']
    form: FormGroup;
    fondo: IFondo;
    title: string;
    isChecked = 0;
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _fondosService: FondosService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        public menu: JedsMenuService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.fondo = this.data ? this.data : null;
        this.title = this.fondo?.idfondo ? 'Editar Fondo' : 'Nuevo Fondo';
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
        
        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
          next: (config: IAlertConfig) => {
            this.showAlert = true;
            this._alertConfig = {
              ...config,
              appearance: 'border',
            };
            let time = 2;
            timer(100, 1500)
              .pipe(
                finalize(() => {
                  this.showAlert = false;
                  this._alertConfig = null;
                }),
                takeWhile(() => time > 0),
                takeUntil(this._unsubscribeAll),
                tap(() => (time -= 1))
              )
              .subscribe();
          },
        });
    }

    newForm() {
        this.form = new FormGroup({
            descripcion: new FormControl((this.fondo ? this.fondo.descripcion : ''), []),
            activo: new FormControl((this.fondo ? (this.fondo.activo ? 1 : 0) : 1)),    //Se hace un if ternario porque en un momento el valor de activo se vuelve literalmente 'si'.
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            this.fondo = {
                ...this.fondo,
                descripcion: this.form.get('descripcion').value,
                activo: this.form.get('activo').value

            }
            if (this.fondo.idfondo) {
                this._fondosService.updateFondo(this.fondo)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            } else {
                this._fondosService.saveFondo(this.fondo)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}